
let avaliao = ""; 
let avaliao_umid = ""; 
let avaliacao_c02


export function avaliacao_Atual(temp, umidade, ppm) {

 if (temp < 15) {
  avaliao = "muito frio";
} else if (temp >= 15 && temp < 20) {
  avaliao = "frio";
} else if (temp >= 20 && temp < 24) {
  avaliao = "confortvel";
} else if (temp >= 24 && temp < 27) {
  avaliao = "levemente quente";
} else if (temp >= 27 && temp <= 35) {
  avaliao = "quente";
} else if (temp >= 35 && temp < 40) {
  avaliao = "extremamente quente";
} else if (temp >= 40) {
  avaliao = "insalubre";
} else {
  avaliao = "sem valor";
}


// avaliao umidade
if (umidade < 20) {
    avaliao_umid = "muito seco";
  } else if (umidade >= 20 && umidade < 30) {
    avaliao_umid = "seco";
  } else if (umidade >= 30 && umidade < 50) {
    avaliao_umid = "confortvel";
  } else if (umidade >= 50 && umidade < 60) {
    avaliao_umid = "levemente umido";
  } else if (umidade >= 60 && umidade <= 70) {
    avaliao_umid = "umido";
  } else if (umidade >= 70 && umidade < 80) {
    avaliao_umid = "muito umido";
  } else if (umidade >= 80) {
    avaliao_umid = "extremamente umido";
  } else {
    avaliao_umid = "sem valor";
  }
  if(ppm<600) {
    avaliacao_c02 = 'Ar externo limpo'
  } else if(ppm >= 600 && ppm < 1000) {
    avaliacao_c02 = 'Ar interno razovel'
  } else if(ppm >= 1000 && ppm < 2000) {
    avaliacao_c02 = 'Ar viciado, precisa ventilar'
  } else if(ppm >= 2000) {
    avaliacao_c02 = 'Alerta, ambiente perigoso'
  } 
 return {
  avaliationTemp: avaliao,  
  avaliationHum: avaliao_umid,
  avaliation_c02: avaliacao_c02
};


}
 



// funes para fazer avaliao dos grficos 



//amplitude
export function amplitude(values) {

const menorValor = Math.min(...values)
const maiorValor = Math.max(...values)

return (maiorValor-menorValor).toFixed(2)
}

//valores exedentes ou picos




export function exedente(values) {
  let e = 0;
  

  for (let i=0;i<values.length;i++) {
   if (values[i]<=15) {
    e++;
   } else if (values[i]>=35) {
    e++
   }
  }

 return ((e/values.length)*100).toFixed(1)


}

export function exedenteHum(values) {
  let e = 0;
  

  for (let i=0;i<values.length;i++) {
   if (values[i]<=20) {
    e++;
   } else if (values[i]>=70) {
    e++
   }
  }

 return ((e/values.length)*100).toFixed(1)


}


 // tendencia do grfico
 


export function tendencias(values) {
  const diferena = values[values.length - 1] - values[0];

  if (diferena > 5) {
    return 'aumento';
  } else if (diferena < -5) {
    return 'baixa';
  } else {
    return 'estabilizado';
  }
}



  
